import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { toggleSerivces } from "../../../redux/functions/detail";
import Checkbox from "../../Input/Checkbox";

export const Services = () => {
    const { isFullTank, isNeedChildChair, isRightWheel } = useSelector(
        (state) => state.order
    );
    const dispatch = useDispatch();
    const onChange = (e) => {
        dispatch(toggleSerivces(e.target.value));
    };
    return (
        <>
            <Checkbox
                name="services"
                value="isFullTank"
                label=" , 500"
                onChange={onChange}
                checked={isFullTank}
            />
            <Checkbox
                name="services"
                value="isNeedChildChair"
                label=" , 200"
                onChange={onChange}
                checked={isNeedChildChair}
            />
            <Checkbox
                name="services"
                value="isRightWheel"
                label=" , 1600"
                onChange={onChange}
                checked={isRightWheel}
            />
        </>
    );
};
