import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { addDuration } from "../../redux/functions/detail";
import NextStep from "../Button/NextStep";

import styles from "./check.module.sass";
import Parameter from "./Parameter";
import Price from "./Price";

export const Check = ({ showData, setShowData, setpopupPost }) => {
    const [term, setTerm] = useState("");
    const {
        cityId,
        pointId,
        carId,
        color,
        fullDay,
        fullHour,
        duration,
        dateTo,
        dateFrom,
        tariff,
        isNeedChildChair,
        isRightWheel,
        isFullTank,
    } = useSelector((state) => state.order);
    const { stage } = useSelector((state) => state.stage);
    const checkData = useRef();
    const dispatch = useDispatch();

    let dataClasses = [styles.data];

    if (showData) {
        dataClasses.push(styles.toggle);
    }

    if (stage === 4) {
        dataClasses = [];
    }

    useEffect(() => {
        fullDay ? setTerm(`${fullDay}${fullHour}`) : setTerm(`${fullHour}`);
    }, [duration]);

    useEffect(() => {
        dispatch(addDuration());
    }, [dateTo, dateFrom]);

    useEffect(() => {
        if (!showData) return;
        const handleClick = (event) => {
            if (
                checkData.current &&
                !checkData.current.contains(event.target)
            ) {
                setShowData(false);
            }
        };

        window.addEventListener("click", handleClick);
        return () => {
            window.removeEventListener("click", handleClick);
        };
    }, [showData]);

    return (
        <div className={styles.check}>
            <div className={dataClasses.join(" ")} ref={checkData}>
                <h3 className={styles.title}> :</h3>
                <Parameter
                    name=" "
                    valueOne={cityId?.name}
                    valueTwo={pointId?.address}
                />
                {carId && <Parameter name="" valueOne={carId?.name} />}
                {color && (
                    <Parameter
                        name=""
                        valueOne={color[0].toUpperCase() + color.slice(1)}
                    />
                )}
                {duration && (
                    <Parameter name=" " valueOne={term} />
                )}
                {tariff && (
                    <Parameter name="" valueOne={tariff.rateTypeId.name} />
                )}
                {isFullTank && <Parameter name=" " valueOne="" />}
                {isNeedChildChair && (
                    <Parameter name=" " valueOne="" />
                )}
                {isRightWheel && (
                    <Parameter name=" " valueOne=" " />
                )}
                {carId && <Price />}
            </div>
            <NextStep setpopupPost={setpopupPost} />
        </div>
    );
};
