import React from "react";
import TabItem from "./TabItem";

import styles from "./tabNavigation.module.sass";

export const TabNavigation = () => {
    return (
        <ul className={styles.tabs}>
            <div className={styles.container}>
                <TabItem value="" tab={1} />
                <TabItem value="" tab={2} />
                <TabItem value="" tab={3} />
                <TabItem value="" tab={4} />
            </div>
        </ul>
    );
};
